/* Mobile Media */

@media (max-width: 992px) {
	.container,
	.container-md,
	.container-sm {
		max-width: 100%;
	}
	.m-none {
		display: none !important;
	}
	.section-header {
		display: none;
	}
	.header-main {
		height: 67px;
	}
	.header-main .col-1 {
		width: auto !important;
		min-width: auto !important;
		max-width: unset;
		flex: unset !important;
		padding: 8px;
	}
	.header-main .row.align-items-center {
		height: 67px;
	}
	.header-main .col-8 {
		flex: unset;
		margin-left: auto;
		padding: 0px;
	}
	.header-main .col-8 .hc-nav-trigger {
		top: 0px;
		right: 8px;
	}
	.d-none {
		display: block !important;
	}
	.restaurant-pic {
		display: none;
	}
	.fixed-bottom-bar {
		padding-bottom: 56px;
	}
	.hc-nav-trigger {
		top: 18px;
		left: 17px;
	}
	footer.section-footer.border-top.bg-dark {
		display: none;
	}
	.pb-5,
	.py-5 {
		padding-bottom: 1rem !important;
	}
	.pt-5,
	.py-5 {
		padding-top: 1rem !important;
	}
	.hc-nav-trigger span,
	.hc-nav-trigger span::before,
	.hc-nav-trigger span::after {
		background: #ffffff !important;
	}
	.login-page video {
		display: none;
	}
	.login-page .px-5.col-10.mx-auto {
		margin: 0px !important;
		padding: 0px !important;
		width: 100% !important;
		flex: unset !important;
		max-width: 100%;
	}
}

