.skeleton {
	/* flex-wrap: wrap;
	justify-content: space-between;
	margin: 0 -20px;
	display: flex; */
}
.skeleton .card {
	padding: 20px 20px 57px;
}

.skeleton .card .header {
	display: flex;
	align-items: center;
}

.skeleton .img {
	position: relative;
	width: 100%;
	height: 160px;
	background: #dfdfdf;
	border-radius: 0;
	overflow: hidden;
}
.skeleton .imgslid {
	position: relative;
	width: 100%;
	height: 258px;
	background: #dfdfdf;
	border-radius: 0;
	overflow: hidden;
	border-radius: 5px;
}
.skeleton .card .content {
	margin-top: 14px;
}

.skeleton .content .line {
	height: 17px;
	width: 100%;
	background: #dfdfdf;
	border-radius: 0;
	margin: 10px 0;
	position: relative;
	overflow: hidden;
}
.skeleton .content .line.line-2 {
	width: 100%;
	height: 13px;
	margin-top: 0px;
}
.skeleton .flex-item {
	width: 100%;
	display: flex;
	align-items: top;
	justify-content: space-between;
}

.skeleton .flex-item .line.line-3 {
	max-width: 60px;
}
.skeleton .content .line.line-3 {
	width: 100%;
	height: 20px;
	margin-top: 18px;
}
.skeleton .img::before,
.skeleton .imgslid::before,
.skeleton .content .line::before {
	content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	background-image: linear-gradient(
		to right,
		#d9d9d9 0%,
		#b3b3b3 20%,
		#d9d9d9 40%,
		#d9d9d9 100%
	);
	background-size: 450px 400px;
	background-repeat: no-repeat;
	animation: shimmer 1.4s linear infinite;
}

@keyframes shimmer {
	0% {
		background-position: -450px 0px;
	}
	100% {
		background-position: 450px 0px;
	}
}
